import { Input, Select } from '@rebass/forms';
import { useEffect, useState } from 'react';
import { useSupportedTokens } from '../../state/supported-tokens/hooks';
import { BillingModelTypes, BMTrialTypes } from '../../utils';
import { supportedLifecycles } from '../SupportedLifeCyle';

const ConfigContent = ({ billingModelId, billingModelType, billingModelDetails, widgetData }) => {
  const supportedTokens = useSupportedTokens();

  const [trialType, setTrialType] = useState(billingModelDetails?.trialType ? billingModelDetails?.trialType : 0);

  useEffect(() => {
    setTrialType(billingModelDetails?.trialType);
  }, [billingModelDetails?.trialType]);

  const isRecurringDynamic = BillingModelTypes[billingModelType] === BillingModelTypes.RecurringDynamic;

  const currencyOptions =
    Object.keys(supportedTokens).length > 0 &&
    Object.keys(supportedTokens)?.map(function (index) {
      return (
        <option key={supportedTokens[index].address} value={supportedTokens[index].address}>
          {supportedTokens[index].symbol}
        </option>
      );
    });

  const onBmNameChange = e => {
    widgetData.bmName.setter(e.target.value);
  };
  const onBmPriceChange = e => {
    widgetData.bmPrice.setter(e.target.value);
  };
  const onCurrencyChanged = e => {
    widgetData.bmCurrency.setter(supportedTokens[e.target.value]);
  };
  const onNoOfPaymentsChange = e => {
    widgetData.bmNoOfPayments.setter(e.target.value);
  };
  const onPeriodChange = e => {
    const duration = supportedLifecycles.find(cycle => Number(cycle.periodInSeconds) === Number(e.target.value));
    if (e.target.id === 'bm-trial-period-frequency') {
      widgetData.bmTrialDuration.setter(duration);
    } else {
      widgetData.bmDurationPeriod.setter(duration);
    }
  };

  const onFrequencyChange = e => {
    widgetData.bmFrequency.setter(e.target.value);
  };

  const onTrailPeriodChange = e => {
    widgetData.bmTrialPeriod.setter(e.target.value);
  };

  const onInitialAmountChanged = e => {
    widgetData.bmInitialAmount.setter(e.target.value);
  };

  const frequencyOptions = supportedLifecycles.map(freq => (
    <option key={freq.periodInSeconds} value={freq.periodInSeconds}>
      {freq.label}
    </option>
  ));

  const trialPeriodDuration = (
    <>
      <span className="attribute">Trial Duration</span>

      <div className="trial-period-frequency">
        <Input
          id="bm-trial-period"
          name="bm-trial-period"
          type="number"
          value={widgetData.bmTrialPeriod.value}
          min="1"
          onChange={onTrailPeriodChange}
          placeholder="enter trial period"
        />
        <Select
          id="bm-trial-period-frequency"
          name="bm-trial-period-frequency"
          defaultValue={widgetData.bmTrialDuration.value.periodInSeconds}
          onChange={onPeriodChange}
        >
          {frequencyOptions}
        </Select>
      </div>
    </>
  );

  return (
    <div className="code-wrap">
      <div className="body">
        <span className="attribute">BillingModelName</span>
        <Input
          id="bm-reference-name"
          name="bm-reference-name"
          type="text"
          value={widgetData.bmName.value}
          onChange={onBmNameChange}
          placeholder="enter billing name"
        />
        <span className="attribute">SellingPrice</span>
        <Input
          id="bm-selling-price"
          name="bm-selling-price"
          type="text"
          value={widgetData.bmPrice.value}
          onChange={onBmPriceChange}
          placeholder="enter selling price"
        />
        <span className="attribute">SellingToken</span>
        <Select
          id="bm-currency"
          name="bm-currency"
          style={{
            width: '100px',
            borderRadius: '4px 0 0 4px',
            background: 'black',
          }}
          defaultValue={widgetData.bmCurrency.value?.address}
          onChange={onCurrencyChanged}
        >
          {currencyOptions}
        </Select>

        {isRecurringDynamic && (
          <>
            <span className="attribute">Number of payment</span>
            <Input
              id="bm-no-of-payments"
              name="bm-no-of-payments"
              type="number"
              min="1"
              value={widgetData.bmNoOfPayments.value}
              onChange={onNoOfPaymentsChange}
              placeholder="enter number of payment"
            />

            <span className="attribute">Frequency</span>
            <div className="frequency">
              <Input
                id="bm-frequency"
                name="bm-frequency"
                type="number"
                value={widgetData.bmFrequency.value}
                min="1"
                onChange={onFrequencyChange}
                placeholder="enter frequency"
              />
              <Select
                id="bm-frequency-period"
                name="bm-frequency-period"
                defaultValue={widgetData.bmDurationPeriod.value.periodInSeconds}
                onChange={onPeriodChange}
              >
                {frequencyOptions}
              </Select>
            </div>
            <div className="trialPeriod">
              {trialType === BMTrialTypes.NoTrial ? '' : trialPeriodDuration}
              {trialType === BMTrialTypes.PaidTrial ? (
                <div>
                  <span className="attribute">Trial Amount</span>
                  <Input
                    id="bm-trial-amount"
                    name="bm-trial-amount"
                    type="number"
                    value={widgetData.bmInitialAmount.value}
                    min="1"
                    onChange={onInitialAmountChanged}
                    placeholder="enter paid trail amount"
                  />
                </div>
              ) : (
                ''
              )}
            </div>
          </>
        )}
      </div>
    </div>
  );
};

export default ConfigContent;
