import { useContext } from 'react';
import styled from 'styled-components';
import { NavToggle } from '../App';
import { useBillingModel, useSubscriptionsPerBM } from '../../state/my-billing-models/hooks';
import { BillingModelTypes, SubscriptionTableTypes } from '../../utils';
import { useLocation } from 'react-router';
import edit from '../../assets/images/edit-icon.svg';
import { Link, useHistory } from 'react-router-dom';
import BillingInfoCard from '../../components/BillingInfoCard';
import MerchantWidgetContainer from '../../components/MerchantWidget';
import SubscriptionsTable from '../../components/SubscriptionsTable';
import ArrowRight from '../../assets/images/back-arrow.svg';
import { AutoColumn } from '../../components/Column';
import Loader from '../../components/Loader';

const StyledLink = styled.a`
  &:hover {
    .sidebar-text {
      color: #7023FF;
    }
    .default {
      display: none;
    }
    .focus {
      display: inline;
    }
  }
  display: flex;
  font-size: 13px;
  margin: auto 0;
  text-decoration: none;
  .focus {
    display: none;
  }
  img {
    margin-right: 10px;
  }
}`;

const MainContent = styled.div`
  &.open {
    padding-left: 220px;
    padding-right: 2rem;
    margin-top: 1rem;
  }

  display: flex;
  width: 100%;
  flex-direction: column;
  padding-left: 100px;
  padding-right: 20px;

  .icon-wrap {
    cursor: pointer;
    float: right;
  }

  .top-wrap > .aside-total {
    float: left;
    width: 60%;
  }

  .loader-wrapper {
    line-height: 30vh;

    svg {
      height: 2rem;
      width: 2rem;
    }
  }
`;

export default function MerchantBillingModelDetails() {
  const selectNavToggle = useContext(NavToggle);
  const history = useHistory();
  const { search } = useLocation();
  const query = new URLSearchParams(search);

  let billingModelType = query.get('billing-model-type');
  let billingModelId = query.get('billing-model-id');

  const subscriptions = useSubscriptionsPerBM(BillingModelTypes[billingModelType], billingModelId);
  const billingModelDetails = useBillingModel(BillingModelTypes[billingModelType], billingModelId);
  // console.log(subscriptions, billingModelDetails);

  const editBillingHandler = billingModelDetails => {
    history.push({
      pathname: `/merchant/${
        billingModelType === String(BillingModelTypes.Single) ? 'single' : 'recurring'
      }-billing/edit-billing-model`,
      search: `?billing-model-type=${billingModelType}&billing-model-id=${billingModelId}`,
      state: billingModelDetails,
    });
  };

  return (
    <>
      <MainContent className={selectNavToggle ? ' open' : ''}>
        {billingModelDetails === null ? (
          <AutoColumn gap="lg" justify="center" style={{ height: '30vh' }}>
            <div className="loader-wrapper">
              <Loader />
            </div>
          </AutoColumn>
        ) : (
          <div className="tap-main">
            <div className="icon-wrap">
              {/* <img src={pause} alt="pause" /> */}
              {/* <img src={stop} alt="stop" /> */}
              {/* <img src={code} alt='code' onClick={() => setModalOpen(!modalOpen)} /> */}
              {billingModelType !== String(BillingModelTypes.SingleDynamic) &&
                billingModelType !== String(BillingModelTypes.RecurringDynamic) && (
                  <img src={edit} alt="edit" onClick={() => editBillingHandler(billingModelDetails)} />
                )}
            </div>
            <div className="top-wrap row">
              <AutoColumn gap="lg" justify="flex-start">
                <StyledLink style={{ color: '#756F82', marginBottom: '1rem' }} as={Link} to={`/merchant/dashboard`}>
                  <img src={ArrowRight} alt="arrow" /> Back to Dashboard
                </StyledLink>
              </AutoColumn>
              <div className="billing-card-info-container col-md-6">
                {billingModelType !== String(BillingModelTypes.SingleDynamic) &&
                  billingModelType !== String(BillingModelTypes.RecurringDynamic) && (
                    <BillingInfoCard billingModelDetails={billingModelDetails} />
                  )}
              </div>
              <div className="widget-container col-md-6">
                <MerchantWidgetContainer
                  billingModelType={BillingModelTypes[billingModelType]}
                  billingModelId={billingModelId}
                  billingModelDetails={billingModelDetails}
                />
              </div>
            </div>
          </div>
        )}

        <SubscriptionsTable
          type={SubscriptionTableTypes.MyCustomers}
          billingModelType={BillingModelTypes[billingModelType]}
          billingModel={billingModelDetails}
          subscriptionsData={subscriptions}
        />
      </MainContent>
    </>
  );
}
