import { useSelector } from 'react-redux';
import { useActiveWeb3React } from '../../hooks';
import { AppState } from '../index';
import { SerializedBillingModel, SerializedSubscriptionDetails } from '../../utils/blockchain-serializers';
import { BillingModelTypes } from '../../utils';

export function useAllMyBillingModels(): SerializedBillingModel[] {
  const { chainId } = useActiveWeb3React();
  const state = useSelector<AppState, AppState['my_billing_models']>(state => state.my_billing_models);

  if (!chainId) return [];

  let billingModels: SerializedBillingModel[] = [];
  for (let bmType in BillingModelTypes) {
    if (!state[chainId] || !state[chainId][bmType]) {
      continue;
    }
    Object.keys(state[chainId][bmType]).map((value, index) => {
      billingModels.push(state[chainId][bmType][value]);
    });
  }

  return billingModels;
}

export function useBillingModel(
  billingModelType: BillingModelTypes,
  billingModelId: string,
): SerializedBillingModel | null {
  const { chainId } = useActiveWeb3React();
  const state = useSelector<AppState, AppState['my_billing_models']>(state => state.my_billing_models);

  if (
    !chainId ||
    !state[chainId] ||
    !state[chainId][billingModelType] ||
    !state[chainId][billingModelType][billingModelId]
  )
    return null;

  return state[chainId][billingModelType][billingModelId];
}

export function useBMSubscription(
  billingModelType: BillingModelTypes,
  billingModelId: string,
  bmSubscriptionId: string,
): SerializedSubscriptionDetails | null {
  const { chainId } = useActiveWeb3React();
  const state = useSelector<AppState, AppState['my_billing_models']>(state => state.my_billing_models);

  if (
    !chainId ||
    !state[chainId] ||
    !state[chainId][billingModelType] ||
    !state[chainId][billingModelType][billingModelId]
  )
    return null;

  return state[chainId][billingModelType][billingModelId].subscriptions.find(el => el.id === bmSubscriptionId);
}

export function useSubscriptionsPerBM(
  billingModelType: BillingModelTypes,
  billingModelId: string,
): SerializedSubscriptionDetails[] {
  const { chainId } = useActiveWeb3React();
  const state = useSelector<AppState, AppState['my_billing_models']>(state => state.my_billing_models);

  if (
    !chainId ||
    !state[chainId] ||
    !state[chainId][billingModelType] ||
    !state[chainId][billingModelType][billingModelId]
  )
    return [];

  return state[chainId][billingModelType][billingModelId].subscriptions;
}

export function useSingleBMCount(): number | null {
  const { chainId } = useActiveWeb3React();
  const state = useSelector<AppState, AppState['my_billing_models']>(state => state.my_billing_models);

  if (
    !chainId ||
    !state[chainId] ||
    !state[chainId][BillingModelTypes.Single] ||
    !state[chainId][BillingModelTypes.SingleDynamic]
  )
    return null;
  const singleCount = Object.keys(state[chainId][BillingModelTypes.Single]).length;
  const singleDynamicCount = Object.keys(state[chainId][BillingModelTypes.SingleDynamic]).length;

  return singleCount + singleDynamicCount;
}

export function useRecurringBMCount(): number | null {
  const { chainId } = useActiveWeb3React();
  const state = useSelector<AppState, AppState['my_billing_models']>(state => state.my_billing_models);
  if (
    !chainId ||
    !state[chainId] ||
    !state[chainId][BillingModelTypes.Recurring] ||
    !state[chainId][BillingModelTypes.RecurringDynamic] ||
    !state[chainId][BillingModelTypes.RecurringFreeTrial] ||
    !state[chainId][BillingModelTypes.RecurringPaidTrial]
  )
    return null;

  const recurringCount = Object.keys(state[chainId][BillingModelTypes.Recurring]).length;
  const recurringDynamicCount = Object.keys(state[chainId][BillingModelTypes.RecurringDynamic]).length;
  const recurringFreeTrialCount = Object.keys(state[chainId][BillingModelTypes.RecurringFreeTrial]).length;
  const recurringPaidTrialCount = Object.keys(state[chainId][BillingModelTypes.RecurringPaidTrial]).length;

  return recurringCount + recurringDynamicCount + recurringFreeTrialCount + recurringPaidTrialCount;
}

export function useAllMyBillingModelsPerBMType(): {
  [billingModelType: string]: {
    [billingModelId: string]: SerializedBillingModel;
  };
} {
  const { chainId } = useActiveWeb3React();
  const state = useSelector<AppState, AppState['my_billing_models']>(state => state.my_billing_models);
  return chainId ? state[chainId] ?? {} : {};
}
