import styled from 'styled-components';
import moment from 'moment';
import { SerializedBillingModel, SerializedSubscriptionDetails } from '../../utils/blockchain-serializers';
import { useSupportedTokens } from '../../state/supported-tokens/hooks';

const TotalWrap = styled.div`
  &:not(:last-child) {
    border-right: 1px solid #ddd;
  }

  span.status {
    display: inline-block;
    width: 8px;
    height: 8px;
    background: #27ae60;
    border-radius: 50%;
  }
  h3 {
    margin: 0;
    font-weight: 500;
  }

  padding: 5px 25px;
  p {
    font-size: 10px;
    color: #959595;
    margin: 0;
  }
  h2 {
    font-size: 10px;
    font-weight: 100;
    display: flex;
    align-items: center;
    white-space: nowrap;
  }
`;

interface BillingInfoCardCardProps {
  billingModelDetails: SerializedBillingModel;
}

const BillingInfoCard = (props?: BillingInfoCardCardProps) => {
  const billingModelDetails = props && props.billingModelDetails ? props.billingModelDetails : null;
  const supportedToken = useSupportedTokens();
  const token = supportedToken[props.billingModelDetails?.token];

  return (
    <>
      <div className="aside-total" style={{ display: 'flex' }}>
        <TotalWrap className="total-earning">
          <h2>Billing Model Name</h2>
          <div style={{ display: 'flex', justifyContent: 'space-between' }}>
            <span>
              {billingModelDetails?.name}
              {Number(billingModelDetails?.creationTime) > 0 && (
                <p>
                  <span className="status" /> Active since{' '}
                  {moment(Number(billingModelDetails?.creationTime) * 1000).format('ll')}
                </p>
              )}
            </span>
          </div>
        </TotalWrap>
        <TotalWrap className="total-earning">
          <h2>Price, Currency</h2>
          <div style={{ display: 'flex', justifyContent: 'space-between' }}>
            <span>
              {token?.decimals ? Number(billingModelDetails?.amount) / 10 ** token.decimals : 'Loading...'}{' '}
              {token?.symbol}
            </span>
          </div>
        </TotalWrap>
        <TotalWrap className="total-earning">
          <h2>Charged</h2>
          <div style={{ display: 'flex', justifyContent: 'space-between' }}>
            <span>
              <h3>
                {billingModelDetails?.subscriptionIDs.length > 0 && token?.decimals
                  ? (Number(billingModelDetails?.amount) / 10 ** token?.decimals) * billingModelDetails?.totalPayments
                  : '0'}{' '}
                {token?.symbol}
                <span className="small" />
              </h3>
            </span>
          </div>
        </TotalWrap>
      </div>
    </>
  );
};

export default BillingInfoCard;
